import React, { Component } from 'react';
import {Consumer} from '../../Context';
import TextInputGroup from '../layout/TextInputGroup';
import axios from 'axios';

 class Addcontact extends Component {
        state={
            name:'',
            email:'',
            phone:'',
            errors:{}
        }
        async componentDidMount(){
            const {id} =this.props.match.params;
            const res =  await axios.get(`https://jsonplaceholder.typicode.com/users/${id}`);

            const contact = res.data
            this.setState({
                email:contact.email,
                name:contact.name,
                phone:contact.phone
            })

        }

    onSumbit=async(dispatch,e)=>{
        e.preventDefault()
        const {email,name,phone} =this.state ;

        if(name===''){
            this.setState(
             {errors:{name:'Name is requiured'}})
             return;
        }
        if(email===''){
            this.setState(
             {errors:{email:'Email is requiured'}})
             return;
        }
        if(phone===''){
            this.setState(
             {errors:{phone:'Phone is requiured'}})
             return;
        }
       

        const updatedContact={
            name,
            email,
            phone
        }
        const {id}=this.props.match.params;
        const res =  await axios.put(`https://jsonplaceholder.typicode.com/users/${id}`
        ,updatedContact);

        dispatch({type:'UPDATE_CONTACT' ,payload:res.data})


        
        this.setState({
            name:'',
            email:'',
            phone:'',
            errors:{}
        })
        this.props.history.push('/')
        }


    onChange=e=>{this.setState(
        {[e.target.name]:e.target.value})}

    render() {
        const {name,email,phone,errors}=this.state;

        return(
     <Consumer>
                {value =>{
                    const {dispatch} =value;
                    return(
            <div className="card mb-3">
                <div className="card-header">Edit contact</div>
                <div className="card-body">
                    <form onSubmit={this.onSumbit.bind(this,dispatch)}>
                     <TextInputGroup
                        label="Name"
                        name='name'
                        placeholder="Enter Name"
                        value={name}
                        onChange={this.onChange}
                        error={errors.name}
                     />
                      <TextInputGroup
                        label="Email"
                        name='email'
                        type='email'
                        placeholder="Enter Email"
                        value={email}
                        onChange={this.onChange}
                        error={errors.email}
                     />
                      <TextInputGroup
                        label="Phone"
                        name='phone'
                        placeholder="Enter Phone"
                        value={phone}
                        onChange={this.onChange}
                        error={errors.phone}
                     />
   
                     <input
                     type="submit"
                     value="Edit Contact"
                     className="btn btn-light btn-block"/>
                    </form>
                </div>
            </div>
                    )
                }}
     </Consumer>
        )

    }
}
export default Addcontact;
